.App {
  text-align: center;
  font-family: Courier;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
    width: 18%;

}
.todo-label{
  font-size: small;
}
.label-todo{
  font-size: medium;
}
.ul-list{
  margin-right: 30px;
  font-size: medium;
}

.input-todo {
  width: 200px;
  height: 50px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
}

.button-add{
  width: 50%;
  height: 30%;
  border-radius: 6px;
  font-family: 'Courier';
  background-color: #61dafb;
  margin-right: 10px;
}
.clear{
  background-color:  #61dafb;
  font-size: 15px; 
  text-decoration: none;
  border-radius: 4px;
  padding: 2px;
  color: black;
  border-color: black;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);


}
.header-h{
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

